<template>
  <div id="app">
    <MyHeader @search="updateSearchQuery" />
    
    <router-view>
</router-view>
<!-- <MoviePage/> -->
      <Footer/>
   
  </div>
</template>

<script>
import '../style/PrimeVue.css'; //   
import  MyHeader from './components/Header/MyHeader.vue';
import  Footer from './components/Footer/MyFooter.vue';

import { mapState, mapActions } from 'vuex';
// 
export default {
  name: 'App',
  components: {
    MyHeader,
    Footer,
 
   
  },
  computed: {
    ...mapState(['movies','filteredMovies']),
  },
  data() {
    return {
      currentPage: 1, //       
      totalPages: 1, //        
    };
  },
  methods: {
    ...mapActions(['fetchMovies', 'selectMovie', 'searchMovies']),
    changePage(page) {
      //   
      this.currentPage = page;
      //     
    },
    updateSearchQuery(query) {
      this.$store.commit('setSearchQuery', query); //   setSearchQuery   Vuex
      this.currentPage = 1;
      this.searchMovies(); //   searchMovies   Vuex
    },
  },
};
</script>


<style>
#app {
  background: #04152d; 

}
